// src/App.js
import React, { useState } from "react";
import CurrentLocation from "./currentLocation";
import CurrentLocations from "./components/CurrentLocations";
import About from './components/About';
import NaturalDisaster from "./components/NaturalDisaster";
import NaturalDisasters from "./components/NaturalDisasters";
import Form from './components/Form';
import "./App.css";

function App() {
  const [prediction, setPrediction] = useState(null);

  const handlePrediction = (data) => {
    setPrediction(data);
  };

  return (
    <React.Fragment>
      <div className="container">
        <CurrentLocation />
      </div>
      
      <div className="form-container">
        <Form onPrediction={handlePrediction} />
      </div>
      
      <div>
        <NaturalDisasters />
      </div>
      
      <div>
        <About />
      </div>
      {prediction && (
        <div className="prediction-results">
          <h4>Prediction Results</h4>
          <p>Flood Chance: {prediction.chance}%</p>
          <p>Area: {prediction.area}</p>
          <p>Date: {new Date(prediction.date).toLocaleDateString()}</p>
        </div>
      )}
    </React.Fragment>
  );
}

export default App;
