// src/Form.js
import React, { useState } from 'react';
import axios from 'axios';
import { Form as BootstrapForm, Button, Alert, Spinner } from 'react-bootstrap';
import './Form.css';
import { API_KEY, BASE_URL } from './apiConfig';

const Form = ({ onPrediction }) => {
  const [location, setLocation] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [result, setResult] = useState(null);

  const handleLocationChange = (e) => setLocation(e.target.value);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setError('');
    setResult(null);

    try {
      // Construct the URL based on the World Weather Online API documentation
      const response = await axios.get(`${BASE_URL}weather`, {
        params: {
          key: API_KEY,
          q: location,
          format: 'json',
        }
      });

      // Log the response for debugging
      console.log('API Response:', response.data);

      if (response.data.error) {
        setError(`API Error: ${response.data.error.message}`);
      } else {
        onPrediction(response.data);
        setResult(response.data);
      }
    } catch (err) {
      console.error('Request Error:', err);
      setError('Error fetching prediction. Please try again later.');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="form-container">
      <h2 className="form-title">Weather Prediction Form</h2>
      <BootstrapForm onSubmit={handleSubmit}>
        <BootstrapForm.Group controlId="location" className="form-group">
          <BootstrapForm.Label>Enter Location</BootstrapForm.Label>
          <BootstrapForm.Control
            type="text"
            placeholder="Enter location"
            value={location}
            onChange={handleLocationChange}
            required
            className="form-control"
          />
        </BootstrapForm.Group>
        <Button 
          variant="primary" 
          type="submit" 
          disabled={loading} 
          className="button-submit"
        >
          {loading ? <Spinner animation="border" size="sm" /> : 'Get Prediction'}
        </Button>
        {error && <Alert variant="danger" className="mt-3">{error}</Alert>}
      </BootstrapForm>

      {result && (
        <div className="prediction-results">
          <h4>Prediction Results</h4>
          <p>Location: {result.location.name}</p>
          <p>Temperature: {result.current.temp_c}°C</p>
          <p>Condition: {result.current.condition.text}</p>
        </div>
      )}
    </div>
  );
};

export default Form;
