import React, { useState, useEffect } from 'react';

function CurrentLocation() {
  const [weatherData, setWeatherData] = useState([]);
  const [city, setCity] = useState(''); // State for city name

  useEffect(() => {
    if (city) {
      fetch(`http://localhost:5000/predict_weather?city=${city}`)
        .then(response => response.json())
        .then(data => {
          console.log(data); // Debugging line to check the data structure
          setWeatherData(data.forecast || []); // Set weather data or empty array if no forecast
        })
        .catch(error => console.error('Error fetching weather data:', error));
    }
  }, [city]); // Fetch data when city changes

  return (
    <div>
      <input
        type="text"
        value={city}
        onChange={(e) => setCity(e.target.value)}
        placeholder="Enter city name"
      />
      <button onClick={() => setCity(city)}>Get Weather</button>
      <ul>
        {weatherData.map((item, index) => (
          <li key={index}>
            Date: {item.dt}, Temp: {item.temp}°C, Weather: {item.weather}, Wind Speed: {item.wind_speed} m/s
          </li>
        ))}
      </ul>
    </div>
  );
}

export default CurrentLocation;
