// src/components/NaturalDisasters.js
import React from 'react';
import NaturalDisaster from './NaturalDisaster';
import earthquakeImage from '../images/earthquake.jpg'; // Add your images in the 'src/images' directory
import floodImage from '../images/flood.jpg';
import Form from './Form';

const NaturalDisasters = () => {
  const earthquakePrecautions = [
    'Drop, Cover, and Hold On during shaking.',
'Stay indoors until the shaking stops.',
'Move to an open area if you are outdoors.',
'If indoors, stay away from windows, heavy furniture, and glass objects.',
'If you are in bed, stay there and protect your head with a pillow.',
'If driving, pull over safely and stay inside the vehicle until shaking stops.',
'If you are in a high-rise building, avoid using elevators and stay away from the walls.',
'After the shaking stops, be cautious of potential aftershocks and hazards like gas leaks or structural damage.',
'Check yourself and others for injuries and provide first aid if needed.',
'Follow emergency services’ instructions and stay informed through reliable sources.'
  ];

  const floodPrecautions = [
  'Move to higher ground immediately.',
  'Avoid walking or driving through flood waters.',
  'Listen to emergency broadcasts for updates.',
  'Secure your home by moving important documents and valuables to higher floors.',
  'Prepare an emergency kit with essentials like water, non-perishable food, medications, a flashlight, and batteries.',
  'Turn off gas, electricity, and water if instructed to prevent hazards.',
  'Follow evacuation orders promptly and safely.',
  'Avoid using electrical appliances that have been exposed to floodwaters until inspected by a professional.',
  'Stay away from downed power lines and report them.',
  'Be cautious of wildlife displaced by flooding.',
  'Check on elderly or disabled neighbors if it is safe to do so.'
  ];

  return (
    <div className="natural-disasters">
      <NaturalDisaster
        title="Earthquake"
        image={earthquakeImage}
        precautions={earthquakePrecautions}
      />
      <NaturalDisaster
        title="Flood"
        image={floodImage}
        precautions={floodPrecautions}
      />
      
      
    </div>
  );
};

export default NaturalDisasters;
